import React from 'react';
import { Controller, RenderContentProps } from '@ali/4ever-cangjie';
import { TextBackgroundPluginConfigs } from './types';
import { ProbeProvider } from './probe';

const createRenderContent = (config?: TextBackgroundPluginConfigs) => {
  // 不支持动态
  const { disableAntiAliasing } = config || {};
  return (props: RenderContentProps, controller: Controller, next) => {
    // TODO: 出于性能考虑，这里应该是默认关闭比较好 @多鱼
    if (disableAntiAliasing && disableAntiAliasing()) return next();
    return (
      <ProbeProvider controller={controller}>
        {next()}
      </ProbeProvider>
    );
  };
};

export default createRenderContent;
